"use client"

import type React from "react"
import { useState, useEffect } from "react"
import { Button } from "@/components/ui/button"
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import { Textarea } from "@/components/ui/textarea"
import { Calendar } from "@/components/ui/calendar"
import { Popover, PopoverContent, PopoverTrigger } from "@/components/ui/popover"
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select"
import { CalendarIcon, Clock, Loader2 } from "lucide-react"
import { format, addDays, isBefore, startOfDay } from "date-fns"
import { useRouter } from "next/navigation"
import { cn } from "@/lib/utils"
import { createClient } from "@/lib/supabase/client"

interface BookingFormProps {
  shopId: string
  shopName: string
  diagnosisId?: string
  userEmail?: string
  userName?: string
}

export function BookingForm({ shopId, shopName, diagnosisId, userEmail, userName }: BookingFormProps) {
  const router = useRouter()
  const [date, setDate] = useState<Date>()
  const [time, setTime] = useState("")
  const [name, setName] = useState(userName || "")
  const [email, setEmail] = useState(userEmail || "")
  const [phone, setPhone] = useState("")
  const [notes, setNotes] = useState("")
  const [isSubmitting, setIsSubmitting] = useState(false)
  const [error, setError] = useState("")
  const [timeSlots, setTimeSlots] = useState<string[]>([])
  const [loadingSlots, setLoadingSlots] = useState(false)
  const [maxAdvanceDays, setMaxAdvanceDays] = useState(30)
  const [alternativeTimes, setAlternativeTimes] = useState<string[]>([])
  const [alternativeDates, setAlternativeDates] = useState<string[]>([])

  // Fetch shop preferences on mount to set calendar limits
  useEffect(() => {
    const fetchShopPreferences = async () => {
      try {
        const response = await fetch(`/api/bookings/available-slots?shop_id=${shopId}&date=${format(new Date(), "yyyy-MM-dd")}`)
        const data = await response.json()
        if (data.advance_booking_days) {
          setMaxAdvanceDays(data.advance_booking_days)
        }
      } catch (err) {
        console.error("Error fetching shop preferences:", err)
      }
    }
    fetchShopPreferences()
  }, [shopId])

  // Fetch available time slots when date is selected
  useEffect(() => {
    if (date) {
      fetchAvailableSlots(date)
    } else {
      setTimeSlots([])
      setTime("")
    }
  }, [date, shopId])

  // Set up real-time subscription for booking changes
  useEffect(() => {
    if (!date || !shopId) return

    const supabase = createClient()
    const dateStr = format(date, "yyyy-MM-dd")

    // Subscribe to booking changes for this shop and date
    const channel = supabase
      .channel(`bookings-${shopId}-${dateStr}`)
      .on(
        "postgres_changes",
        {
          event: "*", // Listen to INSERT, UPDATE, DELETE
          schema: "public",
          table: "bookings",
          filter: `shop_id=eq.${shopId}`,
        },
        (payload) => {
          // Only refresh if the booking is for the selected date
          const bookingDate = payload.new?.appointment_date || payload.old?.appointment_date
          if (bookingDate === dateStr) {
            console.log("Booking change detected, refreshing available slots...")
            // Debounce the refresh to avoid too many requests
            setTimeout(() => {
              fetchAvailableSlots(date)
            }, 500)
          }
        }
      )
      .subscribe()

    return () => {
      supabase.removeChannel(channel)
    }
  }, [date, shopId])

  const fetchAvailableSlots = async (selectedDate: Date) => {
    setLoadingSlots(true)
    setTime("")
    setError("") // Clear previous errors
    try {
      const dateStr = format(selectedDate, "yyyy-MM-dd")
      const response = await fetch(`/api/bookings/available-slots?shop_id=${shopId}&date=${dateStr}`)
      const data = await response.json()

      if (response.ok && data.availableSlots) {
        setTimeSlots(data.availableSlots)
        if (data.advance_booking_days) {
          setMaxAdvanceDays(data.advance_booking_days)
        }
        // Clear error if slots are available
        if (data.availableSlots.length > 0) {
          setError("")
        } else if (data.message) {
          setError(data.message)
        }
      } else {
        setTimeSlots([])
        if (data.message) {
          setError(data.message)
        }
      }
    } catch (err) {
      console.error("Error fetching available slots:", err)
      setTimeSlots([])
      setError("Failed to load available time slots. Please try again.")
    } finally {
      setLoadingSlots(false)
    }
  }

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    setError("")
    setAlternativeTimes([])
    setAlternativeDates([])

    if (!date || !time || !name || !email) {
      setError("Please fill in all required fields")
      return
    }

    setIsSubmitting(true)

    try {
      const response = await fetch("/api/bookings", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({
          shop_id: shopId,
          diagnosis_id: diagnosisId || null,
          appointment_date: format(date, "yyyy-MM-dd"),
          appointment_time: time,
          user_name: name,
          user_email: email,
          user_phone: phone || null,
          notes: notes.trim() || null,
        }),
      })

      const data = await response.json()

      if (!response.ok) {
        // Handle alternative suggestions
        if (data.alternativeTimes && data.alternativeTimes.length > 0) {
          setAlternativeTimes(data.alternativeTimes)
          setError(`${data.error} Suggested alternative times: ${data.alternativeTimes.join(", ")}`)
        } else if (data.alternativeDates && data.alternativeDates.length > 0) {
          setAlternativeDates(data.alternativeDates)
          setError(`${data.error} Suggested alternative dates: ${data.alternativeDates.map((d: string) => format(new Date(d), "PPP")).join(", ")}`)
        } else {
          setError(data.error || "Failed to create booking")
        }
        setIsSubmitting(false)
        return
      }

      // Show success message
      const successMessage = data.message || `Booking confirmed for ${shopName}! You'll receive a confirmation email shortly.`
      
      // Refresh available slots to reflect the new booking before resetting
      if (date) {
        // Wait a moment for the database to update, then refresh slots
        setTimeout(async () => {
          await fetchAvailableSlots(date)
          // Reset form after refreshing slots
          setDate(undefined)
          setTime("")
          setNotes("")
          setAlternativeTimes([])
          setAlternativeDates([])
        }, 1000)
      } else {
        // Reset form immediately if no date
        setDate(undefined)
        setTime("")
        setNotes("")
        setAlternativeTimes([])
        setAlternativeDates([])
      }

      alert(successMessage)
      router.refresh()
    } catch (err) {
      setError(err instanceof Error ? err.message : "Failed to create booking")
    } finally {
      setIsSubmitting(false)
    }
  }

  const handleUseAlternativeTime = (altTime: string) => {
    setTime(altTime)
    setAlternativeTimes([])
    setError("")
  }

  const handleUseAlternativeDate = (altDate: string) => {
    setDate(new Date(altDate))
    setAlternativeDates([])
    setError("")
  }

  return (
    <Card>
      <CardHeader>
        <CardTitle>Book an Appointment</CardTitle>
      </CardHeader>
      <CardContent>
        <form onSubmit={handleSubmit} className="space-y-4">
          <div className="space-y-2">
            <Label htmlFor="name">Name *</Label>
            <Input id="name" value={name} onChange={(e) => setName(e.target.value)} required />
          </div>

          <div className="space-y-2">
            <Label htmlFor="email">Email *</Label>
            <Input id="email" type="email" value={email} onChange={(e) => setEmail(e.target.value)} required />
          </div>

          <div className="space-y-2">
            <Label htmlFor="phone">Phone (Optional)</Label>
            <Input id="phone" type="tel" value={phone} onChange={(e) => setPhone(e.target.value)} />
          </div>

          <div className="space-y-2">
            <Label>Appointment Date *</Label>
            <Popover>
              <PopoverTrigger asChild>
                <Button
                  variant="outline"
                  className={cn("w-full justify-start text-left font-normal", !date && "text-muted-foreground")}
                >
                  <CalendarIcon className="mr-2 h-4 w-4" />
                  {date ? format(date, "PPP") : "Pick a date"}
                </Button>
              </PopoverTrigger>
              <PopoverContent className="w-auto p-0">
                <Calendar
                  mode="single"
                  selected={date}
                  onSelect={setDate}
                  disabled={(date) => {
                    const today = startOfDay(new Date())
                    const maxDate = addDays(today, maxAdvanceDays)
                    return isBefore(date, today) || isBefore(maxDate, date)
                  }}
                  initialFocus
                />
              </PopoverContent>
            </Popover>
          </div>

          <div className="space-y-2">
            <Label htmlFor="time">Appointment Time *</Label>
            <div className="relative">
              <Clock className="absolute left-3 top-1/2 h-4 w-4 -translate-y-1/2 text-muted-foreground" />
              {loadingSlots ? (
                <div className="flex h-10 w-full items-center justify-center rounded-md border border-input bg-background px-3 py-2 pl-10 text-sm text-muted-foreground">
                  <Loader2 className="mr-2 h-4 w-4 animate-spin" />
                  Loading available times...
                </div>
              ) : (
                <select
                  id="time"
                  value={time}
                  onChange={(e) => setTime(e.target.value)}
                  className="flex h-10 w-full rounded-md border border-input bg-background px-3 py-2 pl-10 text-sm ring-offset-background focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-ring"
                  required
                  disabled={!date || timeSlots.length === 0}
                >
                  <option value="">
                    {!date 
                      ? "Select a date first" 
                      : timeSlots.length === 0 
                        ? "No available times" 
                        : "Select a time"}
                  </option>
                  {timeSlots.map((slot) => (
                    <option key={slot} value={slot}>
                      {slot}
                    </option>
                  ))}
                </select>
              )}
            </div>
            {date && timeSlots.length > 0 && (
              <p className="text-xs text-muted-foreground">
                {timeSlots.length} available time slot{timeSlots.length !== 1 ? "s" : ""}
              </p>
            )}
          </div>

          <div className="space-y-2">
            <Label htmlFor="notes">Additional Notes (Optional)</Label>
            <Textarea
              id="notes"
              value={notes}
              onChange={(e) => setNotes(e.target.value)}
              placeholder="Any specific issues or requests..."
              rows={3}
            />
          </div>

          {error && (
            <div className="space-y-2">
              <div className="rounded-lg bg-destructive/10 p-3 text-sm text-destructive">{error}</div>
              
              {alternativeTimes.length > 0 && (
                <div className="rounded-lg bg-primary/5 p-3">
                  <p className="mb-2 text-sm font-medium">Suggested alternative times:</p>
                  <div className="flex flex-wrap gap-2">
                    {alternativeTimes.map((altTime) => (
                      <Button
                        key={altTime}
                        type="button"
                        variant="outline"
                        size="sm"
                        onClick={() => handleUseAlternativeTime(altTime)}
                      >
                        {altTime}
                      </Button>
                    ))}
                  </div>
                </div>
              )}

              {alternativeDates.length > 0 && (
                <div className="rounded-lg bg-primary/5 p-3">
                  <p className="mb-2 text-sm font-medium">Suggested alternative dates:</p>
                  <div className="flex flex-wrap gap-2">
                    {alternativeDates.map((altDate) => (
                      <Button
                        key={altDate}
                        type="button"
                        variant="outline"
                        size="sm"
                        onClick={() => handleUseAlternativeDate(altDate)}
                      >
                        {format(new Date(altDate), "MMM d, yyyy")}
                      </Button>
                    ))}
                  </div>
                </div>
              )}
            </div>
          )}

          <Button type="submit" disabled={isSubmitting} className="w-full">
            {isSubmitting ? "Booking..." : "Confirm Booking"}
          </Button>
        </form>
      </CardContent>
    </Card>
  )
}
